:root
{
	--tamu-maroon: #500000;
	--maroon-light: #650000;
	--maroon-light-light: #7a0101;
	--maroon-light-light-light: #930202;
	--maroon-light-light-light-dark: #750101;
	--maroon-light-light-dark: #840101;
}
.App {
  text-align: center;
  font-color: white;
  font-family: Oswald;
  font-weight: 300;

}

body{
