	color: white;
	background-image:
}
div.reveille-anti-transparent
{
	position: absolute;
	width: 100%;
	height: 100%;
	background-color: white;
	opacity: 0.25;
	z-index: -2;
}
div.reveille-container
{
	position: absolute;
	width: 100%;
	height: 100%;
	background-image: url(./img/Reveille.png);
	background-size: cover;
	background-position: 100% 12%;
	background-size: 125%;
	z-index: -3;
}
img.reveille
{
	object-fit: fill;
	width: 100%;
	opacity: 1;
}

div.top-bar-thingy{
	margin-left: auto;
	margin-right: auto;
	position: absolute;
	width: 100%;
	height: 15%;
	background-color: var(--maroon-light);
	z-index:-1;
	transition: 0.5s;

}

div.top-bar-thingy:hover{
	margin-left: auto;
	margin-right: auto;
	position: absolute;
	width: 100%;
	height: 15%;
	background-color: var(--maroon-light-light-light);
	z-index:-1;
	transition: 0.5s;

}
h1.title{
	margin: 0% 25%;
	padding: 2% 0%;
    color: var(white);
	font: Georgia;
	#font-weight: bold;
	font-size: 500%;
    background-color: var(--tamu-maroon);
	transition: .5s;
	font-family: Oswald;
}

h1.title:hover{
	margin: 0% 25%;
	padding: 2% 0%;
    color: var(white);
	font: Georgia;
	#font-weight: bold;
	font-size: 500%;
	opacity: 1;
    background-color: var(--maroon-light-light);
	transition: .5s;
}
h2.top-detective
{
	color: white;
	padding: 1%;
	margin: auto;
	margin-left: 27.5%;
	margin-right: 27.5%;
	background-color: var(--maroon-light);
	transition: 0.5s;
	font-weight: 350;
}

h2.top-detective:hover
{
	color: white;
	padding: 1%;
	margin: auto;
	margin-left: 27.5%;
	margin-right: 27.5%;
	background-color: var(--maroon-light-light);
	transition: 0.5s;
}
div.all-leaderboard
{
	background-color: #e0e0e0aa;
	margin-left: 30%;
	margin-right: 30%;

}

table.leaderboard
{
	color:white;
	background-color:var(--maroon-light-light);
	margin-left: 10%;
	margin-right: 10%;
}

td.detective
{
	margin-bottom: 0.5%;
}

td.detective-name
{
	padding-left: 8%;
	background-color: var(--maroon-light-light-dark);
	font-size: 200%;
	text-align: left;
	transition: 0.5s;
}

td.detective-name:hover
{
	background-color: var(--maroon-light-light-light);
	font-size: 200%;
	text-align: left;
}
td.places-found
{
	width: 20%;
	background-color: var(--maroon-light-light-light);
	font-size: 200%;

	transition: 0.5s;
}

td.places-found:hover
{
	width: 20%;
	background-color: var(--maroon-light-light);
	font-size: 200%;
	transition: 0.5s;

}
th.leaderboard-header
{
	padding-left: 8%;
	font-size: 200%;
	font-weight: normal;
	text-align: left;
	background-color: var(--maroon-light-light-light-dark);
	transition: 0.5s;
}

th.leaderboard-header:hover
{
	font-size: 200%;
	font-weight: normal;
	text-align: left;
	background-color: var(--maroon-light-light-light);
	transition: 0.5s;
}
th.leaderboard-header-places
{
	text-align: center;
	transition: 0.5s;
}

th.leaderboard-header-places:hover
{
	text-align: center;
	background-color: var(--maroon-light-light-light);
}
div.places
{
	color: white;
	background-color: maroon;
}


div.places-found
{
	text-align:right;
}
div.top-ranked
{
	color:white;
	size: 100;
	background-color: #993333;
}
